<template>
	<a class="m-tabbar-item" @click="goToRouter" :class="{'is-active':isActive}">
		<span class="m-tabbar-item-icon">
			<slot name="icon-normal"></slot>
		</span>
		<span class="m-tabbar-item-text">
			<slot>首页</slot>
		</span>
	</a>
</template>

<script>
	export default{
		props:{
			id:{
				type:String
			}
		},
		computed:{
			isActive(){
				if(this.$parent.value===this.id){
					return true;
				}
			}
		},
		methods:{
			goToRouter(){
				this.$parent.$emit('input',this.id);
				this.$router.push(this.id);
			}
		}
	}
</script>
<style lang="less">
@import "../assets/less/var.less";
	.m-tabbar-item{
		flex-grow: 1;
		text-align: center;
		.m-tabar-item-icon{
			display: block;
			padding-top: 2px;
			img{
				width: 28px;
				height: 28px;
			}
		}
		.m-tabbar-item-text{
			display: block;
			font-size: 10px;
			color: #949494;
		}
		&.is-active{
			.m-tabbar-item-text{
				color:@tabbarActiveColor;
			}
		}
	}
</style>


