
import Vue from 'vue'
import Router from 'vue-router'
import Mine from '@/pages/mine'
import Gongzuo from '@/pages/gongzuo'
import Self from '@/pages/self'
import Happy from '@/pages/happy'
import Shezhi from '@/pages/shezhi'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/happy',
      name: 'Happy',
      component: Happy
    },
    {
      path: '/self',
      name: 'Self',
      component: Self
    },
    {
      path: '/shezhi',
      name: 'Shezhi',
      component: Shezhi
    },
    {
      path: '/gongzuo',
      name: 'Gongzuo',
      component: Gongzuo
    }
  ]
})



